export default {
  data() {
    return {
      bgcFry: "#FFCAD4",
      bgcStew: "#F3F6F4",
      bgcBake: "#F3F6F4",
      bgcDeepFry: "#F3F6F4",
      bgcSalad: "#F3F6F4",
      bgcSteam: "#F3F6F4",
    };
  },
  methods: {
    select_category(data) {
      switch (data) {
        case "fry":
          this.bgcFry = "#FFCAD4";
          this.bgcStew = "#F3F6F4";
          this.bgcBake = "#F3F6F4";
          this.bgcDeepFry = "#F3F6F4";
          this.bgcSalad = "#F3F6F4";
          this.bgcSteam = "#F3F6F4";
          this.$store.commit("select_homepage_category", data);
          break;
        case "deep fry":
          this.bgcFry = "#F3F6F4";
          this.bgcStew = "#F3F6F4";
          this.bgcBake = "#F3F6F4";
          this.bgcDeepFry = "#FFCAD4";
          this.bgcSalad = "#F3F6F4";
          this.bgcSteam = "#F3F6F4";
          this.$store.commit("select_homepage_category", data);
          break;
        case "stew":
          this.bgcFry = "#F3F6F4";
          this.bgcStew = "#FFCAD4";
          this.bgcBake = "#F3F6F4";
          this.bgcDeepFry = "#F3F6F4";
          this.bgcSalad = "#F3F6F4";
          this.bgcSteam = "#F3F6F4";
          this.$store.commit("select_homepage_category", data);
          break;
        case "bake":
          this.bgcFry = "#F3F6F4";
          this.bgcStew = "#F3F6F4";
          this.bgcBake = "#FFCAD4";
          this.bgcDeepFry = "#F3F6F4";
          this.bgcSalad = "#F3F6F4";
          this.bgcSteam = "#F3F6F4";
          this.$store.commit("select_homepage_category", data);
          break;
        case "salad":
          this.bgcFry = "#F3F6F4";
          this.bgcStew = "#F3F6F4";
          this.bgcBake = "#F3F6F4";
          this.bgcDeepFry = "#F3F6F4";
          this.bgcSalad = "#FFCAD4";
          this.bgcSteam = "#F3F6F4";
          this.$store.commit("select_homepage_category", data);
          break;
        case "steam":
          this.bgcFry = "#F3F6F4";
          this.bgcStew = "#F3F6F4";
          this.bgcBake = "#F3F6F4";
          this.bgcDeepFry = "#F3F6F4";
          this.bgcSalad = "#F3F6F4";
          this.bgcSteam = "#FFCAD4";
          this.$store.commit("select_homepage_category", data);
          break;
        default:
          break;
      }
    },
  },
};
